import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationExtras } from '@angular/router';
import { Location } from "@angular/common";
import { ModalController } from '@ionic/angular';
import { DatabaseService } from '../api/database.service';
import { UserService } from '../api/user.service';
import { LoadingController } from '@ionic/angular';
@Component({
  selector: 'app-creditDetails',
  templateUrl: './creditDetails.page.html',
  styleUrls: ['./creditDetails.page.scss'],
})
export class CreditDetailsPage implements OnInit {
  coachCustomerNo: any;
  creditReq: any[] = [];
  nodata: any = '1';

  constructor(
    private DatabaseService: DatabaseService,
    public modalController: ModalController,
    private Router: Router,
    private location: Location,
    private route: ActivatedRoute,
    private UserService: UserService,
    public loadingCtrl: LoadingController
  ) {
    this.route.queryParams.subscribe(params => {
      this.coachCustomerNo = this.Router.getCurrentNavigation().extras.state.data;
    });
  }

  ngOnInit() {
   
  }

  ionViewWillEnter() {
    this.getCreditReq();
  }

  getCreditReq() {
    let xhr = new XMLHttpRequest();
    xhr.open("POST", this.UserService.standardUrl + "/getCreditRequestsByCoachCustomerNo");

    xhr.setRequestHeader("Accept", "application/json");
    xhr.setRequestHeader("Content-Type", "application/json");

    xhr.onload = () => this.getCreditReqCallback(xhr.responseText)

    let data

    data = {
      "coachCustomerNo": this.coachCustomerNo,
      "access_token": window.localStorage.getItem('access_token')
    };

    xhr.send(JSON.stringify(data));
  }

  getCreditReqCallback(res: any) {
    res = JSON.parse(res);
 
  if(res.data.length > 0)
  {
   this.nodata = 0;
   this.creditReq =res.data;
  }else{
    this.nodata = 1;
    this.creditReq =[];
  } 
  }

  gotoDetails(credit_no: any)
{

  let xhr = new XMLHttpRequest();
  xhr.open("POST", this.UserService.standardUrl + "/getCreditPdfByCreditNo");

  xhr.setRequestHeader("Accept", "application/json");
  xhr.setRequestHeader("Content-Type", "application/json");

  xhr.onload = () => this.getCreditPDFCallback(xhr.responseText)

  let data

  data = {
    "creditNo": credit_no,
    "access_token": window.localStorage.getItem('access_token')
  };

  xhr.send(JSON.stringify(data));


}

getCreditPDFCallback(res: any) {
  res = JSON.parse(res);
  console.log(res.pdf);
  if(res.status  == 200 )
  {
  this.navToView('data:application/pdf;base64,' + res.pdf)
  }
}

navToView(valueOfBuchung: any) {
  let navigationExtras: NavigationExtras = {
    state: {
      data: valueOfBuchung
    }
  };
  this.Router.navigate(['/view'], navigationExtras)
}
}
